//  url  
 const BASE_URL = "http://localhost:8080"  //http://xuxing2021.top:8083
 // 
 // {url, method, data} 
export const myRequest = ({url,method,data}) => {
 // return  promise promise
return new Promise((resolve, reject)=>{
	uni.request({
		// url 
		url: BASE_URL + url,
		// (get)
		method: method || "get",
		//  
		data: data || {},
		// 
		success: (res)=>{
		//    !=0() 
			// if(res.data.status != 200){
			// 	return uni.showToast({
			// 		title: "",
			// 		icon:"none"
			// 	})
			// }
			// resolve
			resolve(res);
		},
		// error
		fail: (err)=>{
			return uni.showToast({
				title: "",
				icon:"none"
			})
			reject(err)
		}
	})
})
}
